// pages/jobs.js
import { useEffect, useState } from "react";
import { motion } from "framer-motion";
import { Briefcase, MapPin, DollarSign, Loader2 } from "lucide-react";

export default function JobsPage() {
  const [jobs, setJobs] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState("");
  const [sortOption, setSortOption] = useState("newest");
  const [currentPage, setCurrentPage] = useState(1);
  const jobsPerPage = 6;

  useEffect(() => {
    async function fetchJobs() {
      try {
        const res = await fetch(`${process.env.NEXT_PUBLIC_API_URL}/api/jobs/all`);
        if (!res.ok) throw new Error(`HTTP ${res.status}`);
        const data = await res.json();
        setJobs(data.jobs || []);
      } catch (err) {
        console.error("Error fetching jobs:", err);
        setError("Failed to load jobs. Please try again later.");
      } finally {
        setLoading(false);
      }
    }
    fetchJobs();
  }, []);

  // ✅ Sort jobs before displaying
  const sortedJobs = [...jobs].sort((a, b) => {
    if (sortOption === "newest") return new Date(b.created_at) - new Date(a.created_at);
    if (sortOption === "oldest") return new Date(a.created_at) - new Date(b.created_at);
    if (sortOption === "lowBudget") return Number(a.budget) - Number(b.budget);
    if (sortOption === "highBudget") return Number(b.budget) - Number(a.budget);
    return 0;
  });

  // ✅ Pagination logic
  const indexOfLastJob = currentPage * jobsPerPage;
  const indexOfFirstJob = indexOfLastJob - jobsPerPage;
  const currentJobs = sortedJobs.slice(indexOfFirstJob, indexOfLastJob);
  const totalPages = Math.ceil(sortedJobs.length / jobsPerPage);

  const handlePageChange = (page) => setCurrentPage(page);

  return (
    <div className="min-h-screen bg-gradient-to-b from-orange-50 to-white text-black py-16 px-6">
      <div className="max-w-5xl mx-auto text-center mb-10">
        <motion.h1
          initial={{ opacity: 0, y: -20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.5 }}
          className="text-4xl font-bold text-orange-600 mb-2"
        >
          Available Jobs
        </motion.h1>
        <p className="text-gray-600">Browse all posted jobs and find your next task.</p>
      </div>

      {/* 🔽 Sorting Dropdown */}
      <div className="max-w-5xl mx-auto mb-8 flex justify-end">
        <select
          value={sortOption}
          onChange={(e) => setSortOption(e.target.value)}
          className="border border-orange-200 rounded-xl px-3 py-2 text-sm focus:outline-none focus:ring-2 focus:ring-orange-400 bg-white"
        >
          <option value="newest">Newest First</option>
          <option value="oldest">Oldest First</option>
          <option value="lowBudget">Lowest Budget</option>
          <option value="highBudget">Highest Budget</option>
        </select>
      </div>

      {loading ? (
        <div className="flex justify-center items-center h-64">
          <Loader2 className="animate-spin text-orange-600 w-10 h-10" />
        </div>
      ) : error ? (
        <div className="text-center text-red-500">{error}</div>
      ) : currentJobs.length === 0 ? (
        <div className="text-center text-gray-500">No jobs available yet.</div>
      ) : (
        <>
          <motion.div
            className="max-w-5xl mx-auto grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-6"
            initial="hidden"
            animate="visible"
            variants={{
              hidden: { opacity: 0 },
              visible: { opacity: 1, transition: { staggerChildren: 0.1 } },
            }}
          >
            {currentJobs.map((job) => (
              <motion.div
                key={job.id}
                variants={{ hidden: { opacity: 0, y: 30 }, visible: { opacity: 1, y: 0 } }}
                whileHover={{ scale: 1.03 }}
                className="bg-white border border-orange-100 rounded-2xl shadow-md hover:shadow-lg transition p-5 flex flex-col justify-between"
              >
                <div>
                  <h2 className="text-xl font-semibold text-orange-700 mb-2 flex items-center gap-2">
                    <Briefcase className="w-5 h-5 text-orange-500" /> {job.service}
                  </h2>
                  <p className="text-gray-700 mb-3">{job.description}</p>
                  <div className="flex items-center text-sm text-gray-500 gap-2 mb-2">
                    <MapPin className="w-4 h-4 text-orange-400" /> {job.district}
                  </div>
                  <div className="flex items-center text-sm text-gray-500 gap-2">
                    <DollarSign className="w-4 h-4 text-orange-400" />{" "}
                    <span>{Number(job.budget).toFixed(2)} USD</span>
                  </div>
                </div>

                <div className="mt-4 text-right">
                  <span
                    className={`inline-block px-3 py-1 text-sm rounded-full ${
                      job.status === "pending"
                        ? "bg-yellow-100 text-yellow-700"
                        : "bg-green-100 text-green-700"
                    }`}
                  >
                    {job.status}
                  </span>
                </div>
              </motion.div>
            ))}
          </motion.div>

          {/* 🔽 Pagination Controls */}
          <div className="flex justify-center mt-10 gap-2">
            {[...Array(totalPages)].map((_, i) => (
              <button
                key={i}
                onClick={() => handlePageChange(i + 1)}
                className={`px-4 py-2 rounded-lg border ${
                  currentPage === i + 1
                    ? "bg-orange-500 text-white border-orange-500"
                    : "bg-white text-orange-600 border-orange-300 hover:bg-orange-100"
                } transition`}
              >
                {i + 1}
              </button>
            ))}
          </div>
        </>
      )}
    </div>
  );
}
